import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Button, Text, Input } from '@chakra-ui/react';

interface CustomerRegistrationViewPageProps {
    location?: {
        state:{
            company: string,
            jobType: string,
            name:string,
            position:string,
            mail:string,
            tell:string,
            address:string,
            postal_code:string,
        }
    }
}

const CustomerRegistrationViewPage = (props: CustomerRegistrationViewPageProps) => {
    let { location } = props;
    let company = "";
    let jobType = "";
    let name = "";
    let position = "";
    let mail = "";
    let tell = "";
    let address = "";
    let postal_code = ""
    if (location?.state) {
        company = location.state.company;
        jobType = location.state.jobType;
        name = location.state.name;
        position = location.state.position;
        mail = location.state.mail;
        tell = location.state.tell;
        address = location.state.address;
        postal_code = location.state.postal_code;
    }

    const history = useHistory();

    const [companyInputState, setCompanyInputState] = useState(company);
    const [jobTypeInputState, setJobTypeInputState] = useState(jobType);
    const [nameInputState, setNameInputState] = useState(name);
    const [positionInputState, setPositionInputState] = useState(position);
    const [mailInputState, setMailInputState] = useState(mail);
    const [tellInputState, setTellInputState] = useState(tell);
    const [addressInputState, setAddressInputState] = useState(address);
    const [postalcodeInputState, setPostalCodeInputState] = useState(postal_code);

    return (
        <Center flexDirection="column">
            <Box w="269px" margin="15px">
                <Text fontSize="22px" fontWeight="bold">
                    新規顧客の追加
                </Text>
            </Box>
            <Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">企業名</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={companyInputState}
                        onChange={(e) => {
                            setCompanyInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">業種</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={jobTypeInputState}
                        onChange={(e) => {
                            setJobTypeInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">担当者名</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={nameInputState}
                        onChange={(e) => {
                            setNameInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">役職</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={positionInputState}
                        onChange={(e) => {
                            setPositionInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">メールアドレス</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={mailInputState}
                        onChange={(e) => {
                            setMailInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">電話番号</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={tellInputState}
                        onChange={(e) => {
                            setTellInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">郵便番号</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={postalcodeInputState}
                        onChange={(e) => {
                            setPostalCodeInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">住所</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={addressInputState}
                        onChange={(e) => {
                            setAddressInputState(e.target.value);
                        }}
                    />
                </Box>
            </Box>
            <Button
                w="260px"
                h="40px"
                marginTop="20px"
                color="#504E4E"
                bgColor="white"
                border="2px"
                borderColor="#504E4E"
                onClick={() => {
                    history.push({
                        pathname: '/registration-Review',
                        state: {
                            company: companyInputState,//corporation _name
                            jobType: jobTypeInputState,//sector
                            name: nameInputState,//name
                            position: positionInputState,//postion
                            mail: mailInputState,//mail
                            tell: tellInputState,//phone
                            address: addressInputState,//address
                            postal_code: postalcodeInputState//postal_code
                        }
                    });
                }}
            >
                登録内容の確認
            </Button>
        </Center>
    );
}

export default CustomerRegistrationViewPage;