export interface Units{
    unit_id: string;
    customer_name: string;
    department_name: string;
    contract_id: number;
    is_charging: boolean;
    is_working: boolean;
    soc: number;
    required_action: string;
    errorcode: number;
    profile: {
        unit_type: string;
        purpose: string;
        location: {
            latitude: number;
            longitude: number;
        }
    };
}

export interface Unit {
    unit_id: string;
    error:{
        error_code:{
            Int32: number;
            Valid: boolean;
        };
        required_action: string;
        charger_error: number;
        error_message: string;
    };
    //errorcode: number;
    profile: {
        unit_type: string;
        purpose: string;
        location: {
            latitude: number;
            longitude: number;
        }
    };
    status:{
        is_charging: boolean;
        is_working: boolean;
        last_io_time: number;
        max_cell_voltage: number;
        max_temperature: number;
        min_cell_voltage: number;
        min_temperature: number;
        number_of_charges: number;
        output_current: number;
        output_voltage: number;
        soc: number;
        usage_time: number;
        current: number;
        voltage: number;

    }
    time_stamps:{
        registerd_at:number;
        time:number;
    }
    last_io_time: number;
    customer_id:number;
    corporation_name: string;
}

