import React, {useState} from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Button, Text, Input } from '@chakra-ui/react';

interface CustomerDepartmentRegistrationViewPageProps {
    location: {
        state:{
            company: string,//company name props
            account_id: number//account_id props
            //以下はcostomerと共通の項目がありそうならのちに継承項目として入れる
            officeName?: string,
            uptime?: string,
            weeklyHoliday?: string,
            chargeName?: string,
            position?: string,
            mail?: string,
            tell?: string,
            postal?: string,
            address?: string,
        }
    }
}

const CustomerDepartmentRegistrationViewPage = (props: CustomerDepartmentRegistrationViewPageProps) => {
    //形象により決まって、DBに載せるもの
    const { location } = props;
    const company = location.state.company;//company name->いらない？
    const account_id = location.state.account_id;//=parent_id
    //console.log("props account_id is:"+account_id)

    //入力するもの
    const [officeNameInputState, setOfficeNameInputState] = useState(location.state.officeName || "");
    const [uptimeInputState, setUptimeInputState] = useState(location.state.uptime || "");
    const [weeklyHolidayInputState, setWeeklyHolidayInputState] = useState(location.state.weeklyHoliday || "");
    const [chargeNameInputState, setChargeNameInputState] = useState(location.state.chargeName || "");
    const [positionInputState, setPositionInputState] = useState(location.state.position || "");
    const [mailInputState, setMailInputState] = useState(location.state.mail || "");
    const [tellInputState, setTellInputState] = useState(location.state.tell || "");
    const [postalInputState, setPostalInputState] = useState(location.state.postal || "");
    const [addressInputState, setAddressInputState] = useState(location.state.address || "");

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Box w="269px" margin="15px">
                <Text fontSize="22px" fontWeight="bold">
                    新事業所登録
                </Text>
            </Box>
            <Box w="269px" marginBottom="15px">
                <Text fontSize="16px" fontWeight="bold">
                    企業名
                </Text>
                <Text fontSize="16px" fontWeight="bold">
                    {company}
                </Text>
            </Box>
            <Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">事業所名</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={officeNameInputState}
                        onChange={(e) => {
                            setOfficeNameInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">1日の稼働時間</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={uptimeInputState}
                        onChange={(e) => {
                            setUptimeInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">週休</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={weeklyHolidayInputState}
                        onChange={(e) => {
                            setWeeklyHolidayInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">担当者名</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={chargeNameInputState}
                        onChange={(e) => {
                            setChargeNameInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">役職</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={positionInputState}
                        onChange={(e) => {
                            setPositionInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">メールアドレス</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={mailInputState}
                        onChange={(e) => {
                            setMailInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">電話番号</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={tellInputState}
                        onChange={(e) => {
                            setTellInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">郵便番号</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={postalInputState}
                        onChange={(e) => {
                            setPostalInputState(e.target.value);
                        }}
                    />
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">住所</Text>
                    <Input
                        w="269px"
                        h="28px"
                        value={addressInputState}
                        onChange={(e) => {
                            setAddressInputState(e.target.value);
                        }}
                    />
                </Box>
            </Box>
            <Button
                w="260px"
                h="40px"
                marginTop="20px"
                color="#504E4E"
                bgColor="white"
                border="2px"
                borderColor="#504E4E"
                onClick={() => {
                    history.push({
                        pathname: '/department-review',
                        state: {
                            company: company,//customer/company_name 表示用
                            account_id: account_id,//DBから現状のdepartmentを取ってくるため→department_idを次ページで決定

                            officeName: officeNameInputState,//department_name
                            uptime: uptimeInputState,//daily_working_hour
                            weeklyHoliday: weeklyHolidayInputState,
                            chargeName: chargeNameInputState,//name
                            position: positionInputState,
                            mail: mailInputState,
                            tell: tellInputState,
                            postal: postalInputState,
                            address: addressInputState,
                        }
                    });
                }}
            >
                登録内容の確認
            </Button>
        </Center>
    );
}

export default CustomerDepartmentRegistrationViewPage;