import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Flex, Text } from '@chakra-ui/react';
import axios from "axios"

import {apiURL,Contracts,Customer,Customers,CustomersJson,Units,Departments,DepartmentsJson} from "../../modules/interfaces"

import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";

axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';

interface CustomerRegistrationReviewPageProps {
    location: {
        state:{
            company: string,
            jobType: string,
            name:string,
            position:string,
            mail:string,
            tell:string,
            address:string,
            postal_code:string,
        }
    }
}

const CustomerRegistrationReviewPage = (props: CustomerRegistrationReviewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const jobType = location.state.jobType;
    const name = location.state.name;
    const position = location.state.position;
    const mail = location.state.mail;
    const tell = location.state.tell;
    const address = location.state.address;
    const postal_code = location.state.postal_code;

    const history = useHistory();

    const defaultCustomers:Customers[] = [];
    const [customers, setCustomers]: [Customers[], (customers: Customers[]) => void] = React.useState(defaultCustomers);

    React.useEffect(() => {
        axios
            .get<Customers[]>(apiURL+"api/v1/customers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomers(response.data);
            });
        }, []);
        console.log('customers data should be fetched at this point');
        console.log(customers)

    let addCustomerData:CustomersJson;

    let len=0
    let account_id = 0
    if(customers!=null){
        len = customers.length
    }
    //console.log(len)

    if (len!=0){
        let last_id = customers[len-1].account_id
        //const account_id = customers[-1].account_id
        account_id = last_id + 1
    }else{
        account_id = 1
    }
    //console.log(account_id)
    
    addCustomerData={
        account_id:String(account_id),
        corporation_name:company,
        sector:jobType,
        name:name,
        position:position,
        postal_code:postal_code,
        address:address,
        mail:mail,
        phone:tell,
    }

    return (
        <Center flexDirection="column">
            <Box w="269px" margin="15px">
                <Text fontSize="22px" fontWeight="bold">
                    登録内容の確認
                </Text>
            </Box>
            <Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">企業名</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {company}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">業種</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {jobType}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">担当者名</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {name}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">役職</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {position}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">メールアドレス</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {mail}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">電話番号</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {tell}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">住所</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {postal_code}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">住所</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {address}
                    </Text>
                </Box>
            </Box>
            <Flex
                w="275px"
                marginTop="15px"
                direction="row"
                justifyContent="space-between">
                <BackButton
                    onClick={()=>{
                        history.push({
                            pathname: '/registration-view',
                            state: {
                                company: company,
                                jobType: jobType,
                                name: name,
                                position: position,
                                mail: mail,
                                tell: tell,
                                postal_code:postal_code,
                                address: address,
                            }
                        });
                    }}
                />
                <SubmitButton
                    onClick={()=>{
                        //TODO:企業情報をDBに保存
                        //rest apiで登録
                        //jsonでデータ作成
                        //postで送信
                        
                        console.log("json data")
                        console.log(addCustomerData)
 
                        axios
                            .post(apiURL+"api/v1/customer/post/", addCustomerData,
                            {
                                headers: {
                                    "Content-Type": "applicaton/json",
                                    //"Content-Type": "text/plain",
                                },
                            }
                            )
                            .then(function (response) {
                                console.log(response.data)
                                })
                            .catch(function (error) {
                                console.log(error)
                                })
                            .then(function () {
                                console.log ("*** 終了 ***")
                                })
  
                            console.log('add new customer to DB');

                        history.push({
                            pathname: '/registration-complete',
                            state: {
                                account_id: account_id,
                                company: company,//corporation_name
                                jobType: jobType,
                            }
                        });
                    }}
                />
            </Flex>
        </Center>
    );
}

export default CustomerRegistrationReviewPage;