import React, {useState} from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Input,
    RadioGroup,
    Radio,
    Spacer,
    Flex,
    Checkbox,
} from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,BatteryOption,Charger,ChargerJson,Units} from "../../modules/interfaces"
import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";
import { truncate } from 'node:fs';

interface DepartmentFieldRegistrationChargeViewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
            is_update: boolean,
        },
    }
}

const DepartmentFieldRegistrationChargeViewPage = (props: DepartmentFieldRegistrationChargeViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const is_update = location.state.is_update;

    const [chargeNameState, setChargeNameState] = useState("");
    const [provideMethodState, setProvideMethodState] = useState("");
    const [productNumberState, setProductNumberState] = useState("");
    const [powerAmpState, setPowerAmpState] = useState("");
    const [isNeedFrameState, setIsNeedFrameState] = useState(false);
    const [powsupToChargerLengthState, setPowsupToChargerLengthState] = useState("");
    const [chargerToForkliftLengthState, setChargerToForkliftLengthState] = useState("");

    //charger 最新idを入手
    //chargerデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);

    React.useEffect(() => {
    axios
        .get<Charger[]>(apiURL+"api/v1/chargers/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setChargers(response.data);
        })
    },[]);
    
    console.log(chargers)

    console.log("charger's last_id is")
    let charger_last_id = 0
    if(chargers.length!=0){
        let len =  chargers.length
        charger_last_id  = chargers[len-1].charger_id
    }
    console.log(charger_last_id)

    const history = useHistory();
    return (
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="22px"
                fontWeight="bold"
                margin="20px"
            >
                充電器情報登録
            </Text>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                充電器登録名
            </Text>
            <Input
                w="260px"
                placeholder="例) 充電器A"
                marginBottom="20px"
                value={chargeNameState}
                onChange={(e) => {
                    setChargeNameState(e.target.value)
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                充電器電気供給方法
            </Text>
            <Flex direction="row" marginBottom="10px">
                <RadioGroup onChange={setProvideMethodState} value={provideMethodState}>
                    <Flex w="260px" justifyContent="space-between" direction="row" verticalAlign="center">
                        <Radio value="wiringBoard">
                            <Text
                                fontSize="11px"
                                fontWeight="bold"
                            >
                                配線盤
                            </Text>
                        </Radio>
                        <Radio value="plug">
                            <Text
                                fontSize="11px"
                                fontWeight="bold"
                            >
                                プラグ
                            </Text>
                        </Radio>
                        <Input
                            w="70px"
                            placeholder="品番"
                            value={productNumberState}
                            onChange={(e) => {
                                setProductNumberState(e.target.value)
                            }}
                        />
                    </Flex>
                </RadioGroup>
            </Flex>
            <Input
                w="260px"
                placeholder="その他"
                marginBottom="20px"
                onChange={(e) => {
                    setProvideMethodState(e.target.value)
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                電源アンペア数
            </Text>
            <Flex w="260px" direction="row" marginBottom="20px">
                <Input
                    w="50px"
                    value={powerAmpState}
                    onChange={(e) => {
                        setPowerAmpState(e.target.value)
                    }}
                />
                <Flex direction="column">
                    <Spacer/>
                    <Text
                        fontSize="11px"
                        fontWeight="bold"
                        color="#504E4E"
                    >
                        A
                    </Text>
                </Flex>
            </Flex>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                架台
            </Text>
            <Checkbox
                w="260px"
                marginLeft="20px"
                marginBottom="20px"
                checked={isNeedFrameState}
                onChange={() => {
                    setIsNeedFrameState(!isNeedFrameState);
                }}
            >
                <Text fontSize="11px" fontWeight="bold">
                    必要
                </Text>
            </Checkbox>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                電源供給から充電器設置場所までのケーブル長さ
            </Text>
            <Flex w="260px" direction="row" marginBottom="20px">
                <Input
                    w="70px"
                    value={powsupToChargerLengthState}
                    onChange={(e) => {
                        setPowsupToChargerLengthState(e.target.value);
                    }}
                />
                <Flex direction="column">
                    <Spacer/>
                    <Text
                        fontSize="11px"
                        fontWeight="bold"
                        color="#504E4E"
                    >
                        M
                    </Text>
                </Flex>
            </Flex>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                充電器からフォークリフトまでのケーブル長さ
            </Text>
            <Flex w="260px" direction="row" marginBottom="40px">
                <Input
                    w="70px"
                    value={chargerToForkliftLengthState}
                    onChange={(e) => {
                        setChargerToForkliftLengthState(e.target.value);
                    }}
                />
                <Flex direction="column">
                    <Spacer/>
                    <Text
                        fontSize="11px"
                        fontWeight="bold"
                        color="#504E4E"
                    >
                        M
                    </Text>
                </Flex>
            </Flex>
            <Flex
                w="260px"
                direction="row"
                justifyContent="space-between"
            >
                <BackButton
                    onClick= {() => {
                        history.push({
                            pathname: `/department-registration`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: is_update,
                            }
                        })
                    }}
                />
                <SubmitButton
                    onClick= {() => {
                        let json : ChargerJson;
                        let stand_json="不要"
                        if(isNeedFrameState){
                            stand_json="必要"
                        }
                        json={
                            charger_id: String(charger_last_id+1),
                            department_id: String(department_id),
                            contract_id: "",
                            update_date: "",
                            info_type: "field",
                            charger_name: chargeNameState,
                            charger_environment: "",
                            charger_environment_else: "",
                            how2supply: provideMethodState,
                            supply_plug: productNumberState,
                            supply_else: "",
                            power_supply_ampere: powerAmpState,
                            stand: stand_json,
                            power_supply2charger_cable_length: powsupToChargerLengthState,
                            charger2forklift_cable_length: chargerToForkliftLengthState,
                            charger_setting_help: "",
                            comment: "",
                        }
                        console.log(json)

                        axios
                            .post(apiURL+"api/v1/charger/post/", json,
                                {
                                    headers: {
                                        "Content-Type": "applicaton/json",
                                      //"Content-Type": "text/plain",
                                    },
                                }
                            )
                            .then(function (response) {
                                console.log(response.data)
                                })
                            .catch(function (error) {
                                  console.log(error)
                                })
                            .then(function () {
                                console.log ("*** 終了 ***")
                                })
    
                        console.log('add new chargers to DB');

                        history.push({
                            pathname: `/department-registration`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: true,
                            }
                        })
                    }}
                />
            </Flex>
        </Center>
    );
}

export default DepartmentFieldRegistrationChargeViewPage;