import React from "react";
import { useHistory } from 'react-router-dom';
import {
    Button,
    Drawer,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
    DrawerHeader,
    DrawerBody,
    Spacer,
    Flex,
    Text,
    Center,
    useDisclosure,
    Avatar,
    Popover,
    PopoverBody,
    PopoverTrigger,
    Portal,
    PopoverContent,
    PopoverArrow,
    PopoverHeader
} from "@chakra-ui/react";
import { HamburgerIcon } from '@chakra-ui/icons'
import { useAuth0 } from "@auth0/auth0-react";

import {ThemeColor} from "../utils/ThemeColor"

interface HeaderProps {
    drawerItems: {
        title: string,
        path: string,
    }[]
}

const Header = (props: HeaderProps) => {
    const { drawerItems } = props;
    const history = useHistory();
    const { isOpen, onOpen, onClose } = useDisclosure();

    const {user, logout} = useAuth0();

    return(
        <Center h="62px" padding="15px" bgColor={ThemeColor.robotBlack}>
            <Flex direction="row">
                <Button bgColor={ThemeColor.robotBlack} onClick={onOpen}>
                    <HamburgerIcon color="#ffffff"/>
                </Button>
                <Drawer
                    isOpen={isOpen}
                    placement="left"
                    onClose={onClose}
                >
                    <DrawerOverlay>
                    <DrawerContent
                        color="white"
                        bgColor={ThemeColor.robotBlack}>
                        <DrawerCloseButton />
                        <DrawerHeader>Menu</DrawerHeader>
                        <DrawerBody>
                            {drawerItems.map((value) => {
                                return (
                                    <Button
                                        w="100%"
                                        bgColor={ThemeColor.robotBlack}
                                        onClick={() => {
                                            history.push(value.path);
                                            onClose();
                                        }}
                                    >
                                        {value.title}
                                    </Button>
                                )
                            })}
                        </DrawerBody>
                    </DrawerContent>
                    </DrawerOverlay>
                </Drawer>
                <Text fontWeight="bold" fontSize="22px" color={ThemeColor.robotColor}>
                    ROBOT
                </Text>
            </Flex>
            <Spacer/>
            <Popover>
                <PopoverTrigger>
                    <Avatar
                        size="md"
                        src={user?.picture}
                    />
                </PopoverTrigger>
                <Portal>
                    <PopoverContent w="132px" h="92px" color="#ffffff" bgColor="#000000">
                    <PopoverArrow />
                    <PopoverHeader>
                        <Text fontSize="12px">Sing in as</Text>
                        <Text fontSize="12px" fontWeight="bold">
                            {user?.name}
                        </Text>
                    </PopoverHeader>
                    <PopoverBody>
                        <Button
                            fontSize="14px"
                            w="112px"
                            h="24px"
                            color={ThemeColor.robotColor}
                            bgColor="#000000"
                            onClick={() => logout({ returnTo: window.location.origin })}
                        >
                            SIGN OUT
                        </Button>
                    </PopoverBody>
                    </PopoverContent>
                </Portal>
            </Popover>
        </Center>
    );
}

export default Header;