import React, {useState} from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Radio,RadioGroup, Text, Input, Flex } from '@chakra-ui/react';

import BackButton from "../../components/buttons/BackButton";
import NextButton from "../../components/buttons/NextButton";

interface DepartmentFieldRegistrationView1PageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
            is_update: boolean,
            fieldView1Data?: {
                name: string,
                type: string,
                forkliftType: string,
                voltage: string,
                capacity: string,
                weight: string,
                depth: string,
                width: string,
                height: string,
                batteryCounter: string,
                batteryReach: string,
            }
        }
    }
}

const DepartmentFieldRegistrationView1Page = (props: DepartmentFieldRegistrationView1PageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const is_update = location.state.is_update;

    const [forkliftTypeState, setForkliftTypeState] = useState(location.state.fieldView1Data?.forkliftType || "");
    const [batteryCounterState, setBatteryCounterState] = useState(location.state.fieldView1Data?.batteryCounter || "");
    const [batteryReachState, setBatteryReachState] = useState(location.state.fieldView1Data?.batteryReach || "");

    const [nameInputState, setNameInputState] = useState(location.state.fieldView1Data?.name || "");
    const [typeInputState, setTypeInputState] = useState(location.state.fieldView1Data?.type || "");
    const [voltageInputState, setVoltageInputState] = useState(location.state.fieldView1Data?.voltage || "");
    const [capacityInputState, setCapacityInputState] = useState(location.state.fieldView1Data?.capacity || "");
    const [weightInputState, setWeightInputState] = useState(location.state.fieldView1Data?.weight || "");
    const [depthInputState, setDepthInputState] = useState(location.state.fieldView1Data?.depth || "");
    const [widthInputState, setWidthInputState] = useState(location.state.fieldView1Data?.width || "");
    const [heightInputState, setHeightInputState] = useState(location.state.fieldView1Data?.height || "");

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="22px"
                fontWeight="bold"
                margin="20px"
            >
                車体情報登録 1/3
            </Text>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                現場情報登録
            </Text>
            <Input
                w="260px"
                placeholder="例)車体A"
                marginBottom="15px"
                value={nameInputState}
                onChange={(e) => {
                    setNameInputState(e.target.value);
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                車種
            </Text>
            <Input
                w="260px"
                placeholder="例)フォークリフト"
                marginBottom="15px"
                value={typeInputState}
                onChange={(e) => {
                    setTypeInputState(e.target.value);
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                フォークリフトの場合選択
            </Text>
            <RadioGroup onChange={setForkliftTypeState} value={forkliftTypeState}>
                <Flex
                    w="260px"
                    direction="row"
                    marginBottom="15px"
                >
                    <Radio value="counter">
                        <Text
                            w="130px"
                            fontSize="11px"
                            fontWeight="bold"
                            color="#504E4E"
                        >
                            カウンター式
                        </Text>
                    </Radio>
                    <Radio value="reach">
                        <Text
                            w="130px"
                            fontSize="11px"
                            fontWeight="bold"
                            color="#504E4E"
                        >
                            リーチ式
                        </Text>
                    </Radio>
                </Flex>
            </RadioGroup>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                バッテリー情報
            </Text>
            <Flex
                w="260px"
                direction="row"
                justifyContent="space-between"
                marginBottom="15px"
            >
                <Input
                    w="70px"
                    padding="5px"
                    textAlign="center"
                    placeholder="電圧"
                    value={voltageInputState}
                    onChange={(e) => {
                        setVoltageInputState(e.target.value);
                    }}
                />
                <Input
                    w="70px"
                    padding="5px"
                    textAlign="center"
                    placeholder="容量"
                    value={capacityInputState}
                    onChange={(e) => {
                        setCapacityInputState(e.target.value);
                    }}
                />
                <Input
                    w="70px"
                    padding="5px"
                    textAlign="center"
                    placeholder="重量"
                    value={weightInputState}
                    onChange={(e) => {
                        setWeightInputState(e.target.value);
                    }}
                />
            </Flex>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                バッテリーケースサイズ
            </Text>
            <Flex
                w="260px"
                direction="row"
                justifyContent="space-between"
                marginBottom="15px"
            >
                <Input
                    w="70px"
                    padding="5px"
                    textAlign="center"
                    placeholder="前後幅"
                    value={depthInputState}
                    onChange={(e) => {
                        setDepthInputState(e.target.value);
                    }}
                />
                <Input
                    w="70px"
                    padding="5px"
                    textAlign="center"
                    placeholder="左右幅"
                    value={widthInputState}
                    onChange={(e) => {
                        setWidthInputState(e.target.value);
                    }}
                />
                <Input
                    w="70px"
                    padding="5px"
                    textAlign="center"
                    placeholder="高さ"
                    value={heightInputState}
                    onChange={(e) => {
                        setHeightInputState(e.target.value);
                    }}
                />
            </Flex>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                バッテリー交換方法
            </Text>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
                marginLeft="40px"
            >
                カウンター式の場合
            </Text>
            <RadioGroup onChange={setBatteryCounterState} value={batteryCounterState}>
                <Flex
                    w="260px"
                    direction="row"
                    marginBottom="15px"
                    marginLeft="40px"
                >
                    <Radio value="suspend">
                        <Text
                            w="110px"
                            fontSize="11px"
                            fontWeight="bold"
                            color="#504E4E"
                        >
                            吊り下げ仕様
                        </Text>
                    </Radio>
                    <Radio value="side">
                        <Text
                            w="110px"
                            fontSize="11px"
                            fontWeight="bold"
                            color="#504E4E"
                        >
                            サイド出し仕様
                        </Text>
                    </Radio>
                </Flex>
            </RadioGroup>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
                marginLeft="40px"
            >
                リーチ式の場合
            </Text>
            <RadioGroup onChange={setBatteryReachState} value={batteryReachState}>
                <Flex
                    w="260px"
                    direction="row"
                    justifyContent="space-between"
                    marginBottom="40px"
                    marginLeft="40px"
                >
                    <Radio value="front">
                        <Text
                            w="110px"
                            fontSize="11px"
                            fontWeight="bold"
                            color="#504E4E"
                        >
                            前出し
                        </Text>
                    </Radio>
                    <Radio value="side">
                        <Text
                            w="110px"
                            fontSize="11px"
                            fontWeight="bold"
                            color="#504E4E"
                        >
                            横出し
                        </Text>
                    </Radio>
                </Flex>
            </RadioGroup>
            <Flex
                w="260px"
                direction="row"
                justifyContent="space-between"
            >
                <BackButton
                    onClick={() => {
                        //history.goBack();
                        history.push({
                            pathname: `/department-registration`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: is_update,
                            }
                        });
                    }}
                />
                <NextButton
                    onClick= {() => {
                        history.push({
                            pathname: `/department-registration2`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: is_update,
                                fieldView1Data: {
                                    name: nameInputState,
                                    type: typeInputState,
                                    forkliftType: forkliftTypeState,
                                    voltage: voltageInputState,
                                    capacity: capacityInputState,
                                    weight: weightInputState,
                                    depth: depthInputState,
                                    width: weightInputState,
                                    height: heightInputState,
                                    batteryCounter: batteryCounterState,
                                    batteryReach: batteryReachState,
                                },
                            }
                        });
                    }}
                />
            </Flex>
        </Center>
    );
}

export default DepartmentFieldRegistrationView1Page;