import React, {useEffect, useState} from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Checkbox,
    Text,
    Input,
    Flex,
    Box,
} from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,BatteryOption,BatteryOptionJson} from "../../modules/interfaces"

import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";
import AddButton from "../../components/buttons/AddButton";

interface DepartmentFieldRegistrationView3PageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
            is_update: boolean,
            fieldView1Data: {
                name: string,
                type: string,
                forkliftType: string,
                voltage: string,
                capacity: string,
                weight: string,
                depth: string,
                width: string,
                height: string,
                batteryCounter: string,
                batteryReach: string,
            },
            fieldView2Data: {
                dayH: string,
                dayM: string,
                chargeTime: string,
                isChangeBattery: boolean,
                changeBatteryCount: string,
                isForkliftTemp: boolean,
                isForkliftDust: boolean,
                forkliftEtc: string,
                isBatteryTemp: boolean,
                isBatteryDust: boolean,
                batteryEtc: string,
                isPureCarPlug: boolean,
                carPlugEtc: string,
                isPureBatteryPlug: boolean,
                batteryPlugEtc: string,
            },
        }
    }
}

const DepartmentFieldRegistrationView3Page = (props: DepartmentFieldRegistrationView3PageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const is_update = location.state.is_update;

    const [isNeedChangeBatteryState, setIsNeedChangeBatteryState] = useState(false);
    const [isSuspendMachineBatteryState, setIsSuspendMachineBatteryState] = useState(false);
    const [is3mForkliftState, setIs3mForkliftStateState] = useState(false);
    const [isFacilityState, setIsFacilityState] = useState(false);
    const [isNeedWiringWorkState, setIsNeedWiringWorkState] = useState(false);
    const [noteInputState, setNoteInputState] = useState("");
    const [requestInputState, setRequestInputState] = useState("");

    const history = useHistory();

    useEffect((() => {
        if (!isNeedChangeBatteryState) {
            setIsSuspendMachineBatteryState(false);
            setIs3mForkliftStateState(false);
            setIsFacilityState(false);
        }
    }), [isNeedChangeBatteryState]);

    //battery_option 最新idを入手
    //battery_optionデータの取得
    const defaultOptions:BatteryOption[] = [];
    const [options, setOptions]: [BatteryOption[], (options: BatteryOption[]) => void] = React.useState(defaultOptions);

    React.useEffect(() => {
    axios
        .get<BatteryOption[]>(apiURL+"api/v1/batteryoptions/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setOptions(response.data);
        })
    },[]);
    
    console.log(options)

    console.log("battery option's last_id is")
    let option_last_id = 0
    if(options.length!=0){
        let len =  options.length
        option_last_id  = options[len-1].battery_option_id
    }
    console.log(option_last_id)

    return (
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="22px"
                fontWeight="bold"
                margin="20px"
            >
                車体情報登録 3/3
            </Text>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                バッテリーの交換作業
            </Text>
            <Checkbox
                w="260px"
                marginBottom="15px"
                checked={isNeedChangeBatteryState}
                onChange={() => {
                    setIsNeedChangeBatteryState(!isNeedChangeBatteryState);
                }}
            >
                <Text fontSize="11px" fontWeight="bold">
                    必要
                </Text>
            </Checkbox>
            {
                isNeedChangeBatteryState &&
                <Flex w="260px" direction="column" marginBottom="15px">
                    <Text
                        w="260px"
                        fontSize="11px"
                        fontWeight="bold"
                        color="#504E4E"
                        marginBottom="10px"
                    >
                        必要の場合
                    </Text>
                    <Checkbox
                        marginLeft="20px"
                        marginBottom="10px"
                        checked={isSuspendMachineBatteryState}
                        onChange={() => {
                            setIsSuspendMachineBatteryState(!isSuspendMachineBatteryState);
                        }}
                    >
                        <Text fontSize="11px" fontWeight="bold">
                            吊り下げ装置あり
                        </Text>
                    </Checkbox>
                    <Checkbox
                        marginLeft="20px"
                        marginBottom="10px"
                        checked={is3mForkliftState}
                        onChange={() => {
                            setIs3mForkliftStateState(!is3mForkliftState);
                        }}
                    >
                        <Text fontSize="11px" fontWeight="bold">
                            3M以上マストのフォークリフトあり
                        </Text>
                    </Checkbox>
                    <Checkbox
                        marginLeft="20px"
                        checked={isFacilityState}
                        onChange={() => {
                            setIsFacilityState(!isFacilityState);
                        }}
                    >
                        <Text fontSize="11px" fontWeight="bold">
                            設備なし
                        </Text>
                    </Checkbox>
                </Flex>
            }
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="15px"
            >
                充電器配線設備作業
            </Text>
            <Checkbox
                w="260px"
                marginBottom="25px"
                checked={isNeedWiringWorkState}
                onChange={() => {
                    setIsNeedWiringWorkState(!isNeedWiringWorkState);
                }}
            >
                <Text fontSize="11px" fontWeight="bold">
                    必要
                </Text>
            </Checkbox>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                お客様特有の症状など、特記事項
            </Text>
            <Input
                w="260px"
                marginBottom="20px"
                placeholder="特になし"
                value={noteInputState}
                onChange={(e) => {
                    setNoteInputState(e.target.value)
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                要望・課題
            </Text>
            <Input
                w="260px"
                marginBottom="20px"
                placeholder="特になし"
                value={requestInputState}
                onChange={(e) => {
                    setRequestInputState(e.target.value)
                }}
            />
            <Box marginBottom="50px">
                <AddButton
                    label="フォークリフトの写真"
                    onClick={() => {
                        //TODO:フォークリフトの写真を登録する処理
                    }}
                />
            </Box>
            <Flex
                w="260px"
                direction="row"
                justifyContent="space-between"
            >
                <BackButton
                    onClick={() => {
                        history.push({
                            pathname: `/department-registration2`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                fieldView1Data: location.state.fieldView1Data,
                                fieldView2Data: location.state.fieldView2Data,
                            }
                        });
                    }}
                />
                <SubmitButton
                    onClick= {() => {
                        let json : BatteryOptionJson;
                        let environment_state = ""
                        if(location.state.fieldView2Data.isBatteryTemp){
                            environment_state = "room"
                        }
                        if(location.state.fieldView2Data.isBatteryDust){
                            environment_state = "Dust"
                        }
                        let input_plug_json = ""
                        if(location.state.fieldView2Data.isPureBatteryPlug){
                            input_plug_json = "genuine"
                        }
                        let output_plug_json = ""
                        if(location.state.fieldView2Data.isPureCarPlug){
                            output_plug_json = "genuine"
                        }
                        json = {
                            battery_option_id: String(option_last_id+1),
                            department_id: String(department_id),
                            contract_id: "",
                            update_date: "",
                            info_type: "field",
                            option_name: location.state.fieldView1Data.name,
                            forklift: location.state.fieldView1Data.type,
                            type: location.state.fieldView1Data.forkliftType,
                            serial_number: "",//TODO serialnumber追加
                            voltage: location.state.fieldView1Data.voltage,
                            capacity: location.state.fieldView1Data.capacity,
                            weight: location.state.fieldView1Data.weight,
                            vertical: location.state.fieldView1Data.height,
                            horizontal: location.state.fieldView1Data.width,
                            how2change: location.state.fieldView1Data.batteryCounter,
                            daily_working_minute: location.state.fieldView2Data.dayH,
                            daily_charging_minute: location.state.fieldView2Data.chargeTime,
                            number_of_change: location.state.fieldView2Data.changeBatteryCount,
                            forklift_environment: environment_state,
                            environment_else: location.state.fieldView2Data.batteryEtc,
                            input_plug: input_plug_json,
                            output_plug: output_plug_json,
                            change_help: String(isNeedChangeBatteryState),
                            comment: noteInputState,
                            //pic_forklift: number,
                            //pic_forklift_plate: number,
                            //pic_battery: number,
                            //pic_charger_place: number,
                            //pic_battery_plug: number,
                            //pic_forklift_plug: number,
                            //pic_change_equipment: number,
                        }
                        console.log(json)
                        
                        axios
                            .post(apiURL+"api/v1/batteryoption/post/", json,
                                {
                                    headers: {
                                        "Content-Type": "applicaton/json",
                                        //"Content-Type": "text/plain",
                                    },
                                }
                            )
                            .then(function (response) {
                                console.log(response.data)
                                })
                            .catch(function (error) {
                                console.log(error)
                                })
                            .then(function () {
                                console.log ("*** 終了 ***")
                                })
    
                        console.log('add new battery options to DB');
                        
                        history.push({
                            pathname: `/department-registration`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: true,
                            }
                        })
                        
                    }}
                />
            </Flex>
        </Center>
    );
}

export default DepartmentFieldRegistrationView3Page;