import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Input,
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    Button,
    Flex,
} from '@chakra-ui/react';
import { ChevronDownIcon } from '@chakra-ui/icons'

interface ContractRegistrationViewPageProps {
    location: {
        state:{
            company: string,//corporation name
            officeName: string,
            account_id: number,
            department_id: number,
            contractData?: {
                contractName: string,
                contractStartDate: string,
                contractEndDate: string,
                contractType: string,
            },
        }
    }
}

const ContractRegistrationViewPage = (props: ContractRegistrationViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    //console.log(department_id)

    const [contractNameInputState, setContractNameInputState] = useState(location.state.contractData?.contractName || "");
    const [contractStartDateInputState, setContractStartDateInputState] = useState(location.state.contractData?.contractStartDate || "");
    const [contractEndDateInputState, setContractEndDateInputState] = useState(location.state.contractData?.contractEndDate || "");
    const [contractTypeInputState, setContractTypeInputState] = useState(location.state.contractData?.contractType || "");

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="22px"
                fontWeight="bold"
                margin="20px"
            >
                新規契約登録
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                企業名
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="15px"
            >
                {company}
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                事業所名
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="20px"
            >
                {officeName}
            </Text>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約名
            </Text>
            <Input
                w="260px"
                marginBottom="15px"
                placeholder="例) テスト契約"
                value={contractNameInputState}
                onChange={(e) => {
                    setContractNameInputState(e.target.value)
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約開始年月
            </Text>
            <Input
                w="260px"
                marginBottom="15px"
                placeholder="例) 2021/04/01"
                value={contractStartDateInputState}
                onChange={(e) => {
                    setContractStartDateInputState(e.target.value)
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約終了年月
            </Text>
            <Input
                w="260px"
                marginBottom="15px"
                placeholder="例) 2023/03/31"
                value={contractEndDateInputState}
                onChange={(e) => {
                    setContractEndDateInputState(e.target.value)
                }}
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約タイプ
            </Text>
            <Menu>
                <MenuButton
                    w="260px"
                    h="40px"
                    transition="all 0.2s"
                    borderRadius="md"
                    borderWidth="1px"
                    justifyContent="space-between"
                    marginBottom="60px"
                >
                    <Flex
                        w="260px"
                        justifyContent="space-between"
                    >
                        <Text marginLeft="10px">
                            {
                                contractTypeInputState === "" ? 
                                "選択してください" :
                                contractTypeInputState
                            }
                        </Text>
                        <ChevronDownIcon
                            marginRight="10px"
                            marginTop="4px"
                        />
                    </Flex>
                </MenuButton>
                <MenuList>
                    <MenuItem
                        onClick={() => {
                            setContractTypeInputState("テスト導入");
                        }}
                    >
                        テスト導入
                    </MenuItem>
                    <MenuItem
                        onClick={() => {
                            setContractTypeInputState("売却契約");
                        }}
                    >
                        売却契約
                    </MenuItem>
                    <MenuItem
                        onClick={() => {
                            setContractTypeInputState("リース契約");
                        }}
                    >
                        リース契約
                    </MenuItem>
                </MenuList>
            </Menu>
            <Button
                w="260px"
                h="40px"
                marginTop="20px"
                color="#504E4E"
                bgColor="white"
                border="2px"
                borderColor="#504E4E"
                onClick={() => {
                    history.push({
                        pathname: `/contract-review`,
                        state: {
                            company: company,
                            officeName: officeName,
                            account_id: account_id,
                            department_id: department_id,
                            contractData: {
                                contractName: contractNameInputState,
                                contractStartDate: contractStartDateInputState,
                                contractEndDate: contractEndDateInputState,
                                contractType: contractTypeInputState,
                            },
                        }
                    });
                }}
            >
                登録内容を確認
            </Button>
        </Center>
    );
}

export default ContractRegistrationViewPage;